<template>
  <layout help="【拼】任务须知?" help-link="/pdd/help" :routes="routes">
    <div slot="filter">
      <mt-checklist
        title="任务类型"
        v-model="filter.types"
        :options="types">
      </mt-checklist>
    </div>
  </layout>
</template>

<script>
import Layout from '@/components/layout';

export default {
  name: 'PddLayout',
  components: { Layout },
  data() {
    return {
      filter: {
        types: ['精刷任务', '预约任务', '复购任务'],
      },
      types: ['精刷任务', '预约任务', '复购任务'],
    };
  },
  computed: {
    routes() {
      return this.$store.getters.pddRoutes;
    },
  },
};
</script>



